.Title {
  box-sizing: border-box;
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: baseline;
  padding: 0em;
  margin-bottom: 2em;
  color: #041c36;
  background-image: url("https://watermark.lovepik.com/photo/20211203/large/lovepik-record-collection-picture_501486928.jpg");
}

.Title h1 {
  font-size: 80px;
  line-height: 10px;
  padding-top: 40px;
  text-shadow:1px 1px 10px #FFECD1, 1px 1px 10px #FFECD1;
}

.Title-Subtitle {
  font-size: 30px;
  font-weight: 500;
  color:#041c36;
  text-shadow:1px 1px 10px #FFECD1, 1px 1px 10px #FFECD1;
}

.Title a {
  display: inline-block;
  padding: 0.5em 1em;
  color: #041c36;
  font-weight: bold;
  text-decoration: none;
}

.Title .nav-link-active {
  color: #041c36;
  border-bottom: 2px solid;
}

.nav-link {
  margin: 12px;
}

.Title .nav-section {
    background-color: #FFECD1;
      margin: 2.7rem -9999rem 0rem;
  /* add back negative margin value */
    padding: 0.25rem 9999rem;
}